<template>
  <router-link :to="'/movieDetalis/'+item.id +'/01'">
    <ul class="count">
       <h3 v-if="!item.globalReleased">{{item.comingTitle}}</h3>
      <li>
        <img :src="item.img">
        <div>
          <h3>
            {{item.nm}}
            <i v-if="item.version == 'v3d imax'"></i>
            <span v-if="item.version == 'v3d'"></span>
          </h3>
          <div>
            <span v-if="item.globalReleased">观众评</span>
            <span v-if="item.enm">{{item.enm}}</span>
          <label v-if="item.globalReleased">{{item.sc}}</label>
          <label v-if="!item.globalReleased">{{item.wish}}</label>
          <span v-if="!item.globalReleased">人想看</span>
          </div>
          <p>
            <label>主演</label>
            {{item.star}}
          </p>
          <p v-if="item.rt < time">{{item.showInfo}}</p>
        </div>
        <button class="btn1" v-if="item.rt < time">购票</button>
        <button class="btn2" v-if="item.rt > time">预售</button>
        <!-- <p> {{time}}</p> -->
      </li>
    </ul>

  </router-link>
</template>
<script>
export default {
  name: "FilmItme",
  props: ["item"],
  data() {
    return {
      time:new Date()
      
    };
 },
 created(){
   let year = this.time.getFullYear();
   let mouth = (this.time.getMonth()+1);
   let data = this.time.getDate();
   mouth = mouth < 10 ? "0" + mouth: mouth;
  data = data<10 ?"0"+data:data;
   this.time = `${year}-${mouth}-${data}`
  //  console.log(this.item);
 }
};
</script>
<style lang="less">
a {
  text-decoration: none;
  h3{
    margin: 0;
    font-size: 14px;
    color: #333;
  }
  .count {
    list-style: none;
    padding: 0;
    margin-left: 10px;
    li {
      display: flex;
      align-items: center;
      height: 114px;
      img {
        float: left;
        height: 90px;
      }
      div {
        float: left;
        padding-left: 10px;
        width: 230px;
        overflow: hidden;
        h3 {
          color: #333;
          font-size: 17px;
          margin: 0px;
          i:nth-of-type(1) {
            display: inline-block;
            width: 43px;
            height: 14px;
            background-image: url(data:image/png;base64,);
            background-size: 100% 100%;
          }
          span{
            display: inline-block;
            width: 17px;
            height: 14px;
            background-image:url(data:image/png;base64,);
            background-size: 100% 100%;
          }
        }
        p {
          margin: 0;
          font-size: 13px;
          color: #666;
          label {
            color: #666;
            font-size: 13px;
          }
        }
        label {
          color: #faaf00;
          font-weight: 700;
          font-size: 15px;
        }
        span {
          color: #666;
          font-size: 13px;
        }
      }
        
      .btn1 {
        float: left;
        margin-left: 10px;
        border-radius: 8px;
        background-color:#f03d37;
        color: white;
        border: none; 
      }
      .btn2 {
        float: left;
        margin-left: 10px;
        border-radius: 8px;
        background-color:#3c9fe6;
        color: white;
        border: none; 
      }
    }
  }
}
</style>

