<template>
  <div>
    <div class="show">
      <p>近期最受期待</p>
      <div class="parent-dom">
        <vue-scroll :ops="ops" class="acti"> 
         <ul class="child-dom">
        <li v-for="(items, index) in allmessage" :key="index">
          <img :src="items.img" alt>
          <div>
            <i></i>
          </div>
          <p>{{items.wish}}人想看</p>
          <h3>{{items.nm}}</h3>
          <span>{{items.comingTitle}}</span>
        </li>
      </ul>
       </vue-scroll>
      </div>
      
    </div>
    <FilmItme v-for="(item, index) in message" :key="index" v-bind:item="item"></FilmItme>
  </div>
</template>
<script>
// import { log } from 'util';
import FilmItme from "../components/FilmItme";
export default {
  name: "film",
  components: {
    FilmItme
  },
  data: function() {
    return {
       ops: {
          vuescroll: {},
          scrollPanel: {},
          rail: {},
          bar: {}
        },
      message: null,
      allmessage: null
    };
  },
  updated() {
    console.log(this.$route.params);
  },

  created() {
    var _this = this;
    this.$axios
      .get(
        "http://api.kele8.cn/agent/http://m.maoyan.com/ajax/comingList?ci=20&token=&limit=15"
      )
      .then(function(result) {
        console.log(result);
        _this.message = result.data.coming;
        result.data.coming.forEach(element => {
          var imgUrl =
            "https://p1.meituan.net/128.180/movie/" + element.img.split("/")[5];
          element.img = imgUrl;
        });
      });
    this.$axios
      .get(
        "http://api.kele8.cn/agent/http://m.maoyan.com/ajax/mostExpected?ci=20&limit=10&offset=0&token="
      )
      .then(function(result) {
        _this.allmessage = result.data.coming;
        console.log(result.data.coming);
        result.data.coming.forEach(element => {
          var imgUrl =
            "https://p1.meituan.net/128.180/movie/" + element.img.split("/")[5];
          element.img = imgUrl;
          element.comingTitle = element.comingTitle.split(" ")[0];
        });
        _this.allmessage[5].img =
          "https://p0.meituan.net/170.230/moviemachine/5a813d1a352ead901f710d5065fd4805388329.jpg";
        _this.allmessage[9].img =
          "https://p0.meituan.net/170.230/moviemachine/4e99b803b4a15c1286a7d984f864c981346472.jpg";
        _this.allmessage[4].img =
          "https://p0.meituan.net/170.230/moviemachine/5a813d1a352ead901f710d5065fd4805388329.jpg";
      });
  }
};
</script>
<style lang="less" scoped>
.show{
  
  overflow: hidden;
  p {
    margin: 0 0 10px 10px;
    font-size: 14px;
    color: #333;
    font-weight: 700;
  }
  
  ul {
    list-style: none;
    height: 190px;
    margin: 0;
    padding: 0;
    display: flex;
   width: 1200px;
    overflow-x: scroll;
    li {
      // height: 180px;
      position: relative;
      margin-left: 10px;
      flex-basis: 100px;

      img {
        height:148px;
        position: absolute;
        width: 100px;
        box-shadow: 0 1px 0px 0 #333;
      }
      p {
        position: absolute;
        margin: 0;
        font-size: 11px;
        color: #faaf00;
        position: absolute;
        bottom: 50px;
        // left: 4px;
        // font-weight: 700;
      }
      h3 {
        width: 117px;
        position: absolute;
        margin: 0;
        font-size: 13px;
        bottom: 22px;
      }
      span {
        position: absolute;
        bottom: 0;
      }
      div {
        position: absolute;
        width: 28px;
        height: 28px;
        background-color: #777;
        border-bottom-right-radius: 15px;
        text-align: center;
        i {
          display: inline-block;
          background-image: url(data:image/png;base64,);
          width: 10px;
          height: 10px;
          background-size: 100% 100%;
          z-index: 10;
        }
      }
    }
  }
}
</style>

